@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);
.links-grid-items {
    font-family: 'Roboto', sans-serif;
    display: grid;
    grid-template-columns: 50px 1fr 30px;
    margin: 5px;
    margin-right: 10px;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
}


.links-body-infp {
    height: 50PX;
}


.links-buttons {
    display: grid;
    grid-template-columns: 1fr 1fr;
}

.links-qr-code > svg {
    cursor: pointer;
    width: 50px;
    height: 50px;
    margin-top: 2px;
}
.links-body {
    margin-left: 5px;
}

.links-short-url {
    font-size: 13px;
    font-weight: 700;
}

.links-url {
    font-size: 13px;
    font-weight: 500;
    width: 60vw;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.links-date {
    font-size: 12px;
    font-weight: 400;
    opacity: 0.8;
}

.links-check-qr {
    font-size: 10px;
    margin: 0;
}

.links-qr-code-max {
    width: 100%;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    text-align: center;
    
}

.links-qr-code-max > svg {
    width: 60%;
    height: 60%;
}

.links-qr-code-statistic {
    display: -webkit-flex;
    display: flex;
    font-size: 14px;
    opacity: 0.8;
    -webkit-align-items: center;
            align-items: center;
    margin-left: 5px;
}

.links-qr-code-statistic-all {
    font-family: 'Roboto', sans-serif;
    font-weight: 600;
    font-size: 16px;
    margin: 5px 5px 10px 5px;
}


.links-qr-code-statistic-all-link {
    font-family: 'Roboto', sans-serif;
    font-weight: 500;
    font-size: 14px;
    opacity: 0.8;
    margin: 5px 5px 10px 5px;
}

.links-qr-code-statistic-all-date {
    font-family: 'Roboto', sans-serif;
    font-weight: 500;
    font-size: 13px;
    opacity: 0.8;
    margin: 5px;
}

.links-body-vues {
    display: -webkit-flex;
    display: flex;
    font-size: 10px;
    text-align: center;
    color: #818c99;
    -webkit-align-items:  end;
            align-items:  end;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
}

.links-body-vues > div{
    margin-top: 2px;
}

.next-button {
    display: grid;
    grid-template-columns: 1fr 1fr;
    margin: 5px 10px 5px 10px;
}
.Placeholder  main-menu-text {
    padding: 0!important;
    margin: 0!important;
}

.footer {
    margin: 0!important;
    font-size: 10px;
}

.checkbox-p {
    margin: 0;
    -ms-user-select: none;
    -moz-user-select: none;
    -khtml-user-select: none;
    -webkit-user-select: none;
}
.PanelHeader__content > span {
    font-size: 5vw!important;
}

